import '../../../react-masonry.css'

import React from "react"
import ArtHeader from '../../../components/ArtHeader'
import MasonryGallery from '../../../components/MasonryGallery'
import { spacer } from "../../../common"


class ArtPage extends React.Component {
  constructor(props) {
    super(props)
    this.state = {images: []}
  }
  componentDidMount() {
    const cachebuster = Math.round(Date.now() / 10000)
    const url = `/artworks/json/webp-misc.json?${cachebuster}`
    fetch(url)
    .then(response => response.json())
    .then(images => {
      this.setState({ images })
    })
  }
  render() {
    const { images } = this.state
    return (
      <React.Fragment>
          <ArtHeader generator='Misc. Text-To-Image Systems' totalCount={images.length} />
          <MasonryGallery images={images} />
          <div className="spacer" style={spacer}></div>
      </React.Fragment>
    )
  }
}


export default ArtPage
